import { GallaryItem } from "../components/GallaryItem"
import { TitleBar } from "../components/TitleBar"
import "./HomePage.css"

function HomePage() {
    return (
        <div
            style={{
                backgroundColor: "#f5f5f5",
                display: "flex",
                alignItems: "center",
                flexDirection: "column",
                overflow: "initial",
            }}
        >
            <TitleBar />
            <div
                style={{
                    width: "80%",
                    maxWidth: 1200,
                    marginTop: 80,
                    marginBottom: 80,
                }}
            >
                <div
                    style={{
                        display: "flex",
                        flexDirection: "column",
                        padding: "0 10px 0 10px",
                    }}
                >
                    <h1
                        style={{
                            fontFamily: "Times",
                            fontSize: "30px",
                            fontWeight: "600",
                            borderBottom: "2px #000 solid",
                        }}
                    >
                        GraphDescriptor: An Automatic Description Generator
                        for Node-Link Diagrams
                    </h1>
                    {/* <img
                        alt="main"
                        src="main.jpg"
                        style={{
                            width: "60%",
                            margin: "20px auto 20px auto",
                        }}
                    /> */}
                    <p
                        style={{
                            fontFamily: "Times",
                            fontSize: "1.2em",
                        }}
                    >
                        Node-link diagrams can effectively reveal relations and attributes of entities. However, non-professional users may have a low ability to read visual forms or be unskilled in exploring cluttered diagrams. This paper presents GraphDescriptor, an automatic description generation approach for node-link diagrams. The key idea is to extract relevant information from both the underlying data and the source code of visualization and generate textual descriptions with a template-based scheme. We design and develop an interactive interface for interactive specification, exploration, and modulation of descriptions. Diverse examples and a user study verify the utility and effectiveness of GraphDescriptor.
                    </p>
                </div>
                <div
                    style={{
                        fontFamily: "Times",
                        fontSize: 30,
                        fontWeight: 600,
                        margin: "0 10px 10px 10px",
                        height: 50,
                        borderBottom: "2px #000 solid",
                    }}
                >
                    Gallary
                </div>
                <div
                    style={{
                        display: "flex",
                        flexWrap: "wrap",
                        justifyContent: "space-around",
                    }}
                >
                    <GallaryItem
                        image={"./data/imdb-movies-raw/imdb-movies.png"}
                        title={"IMDb Dataset: Simple Nodes and Links"}
                        route={"/imdb-movies"}
                    >
                        Two nodes are connected if their attributes{" "}
                        <span class="description-parameter">genre</span> have
                        common values. Each{" "}
                        <span class="description-parameter">node</span> consists
                        of only <span class="description-parameter">1</span>{" "}
                        element. It is a{" "}
                        <span class="description-parameter">
                            &lt;circle&gt;
                        </span>
                        . Its "<span class="description-parameter">"fill"</span>
                        " encodes the attribute{" "}
                        <span class="description-parameter">
                            date_published
                        </span>
                        . Its "<span class="description-parameter">"cx"</span>"
                        encodes the attribute{" "}
                        <span class="description-parameter">budget</span>. Its "
                        <span class="description-parameter">"cy"</span>" encodes
                        the attribute{" "}
                        <span class="description-parameter">
                            worlwide_gross_income
                        </span>
                        . Each <span class="description-parameter">link</span>{" "}
                        consists of only{" "}
                        <span class="description-parameter">1</span> element. It
                        is a{" "}
                        <span class="description-parameter">&lt;line&gt;</span>.
                        Its "
                        <span class="description-parameter">
                            "stroke-width"
                        </span>
                        " encodes the attribute{" "}
                        <span class="description-parameter">common_genre</span>
                    </GallaryItem>
                    <GallaryItem
                        image={
                            "./data/imdb-movies-shapes/imdb-movies-shapes.png"
                        }
                        title={"IMDb Dataset: Nodes with Different Shapes"}
                        route={"/imdb-movies-shapes"}
                    >
                        Two nodes are connected if their attributes{" "}
                        <span class="description-parameter">actors</span> have
                        common values. Its "tagName" is varying among multiple
                        shapes:{" "}
                        <span class="description-parameter">
                            &lt;circle&gt;
                        </span>{" "}
                        and{" "}
                        <span class="description-parameter">&lt;rect&gt;</span>.
                        When it turns to{" "}
                        <span class="description-parameter ">
                            &lt;circle&gt;
                        </span>
                        , its encodings are changed. Its{" "}
                        <span class="description-parameter ">r</span> encodes
                        the attribute{" "}
                        <span class="description-parameter ">votes</span>.
                    </GallaryItem>
                    <GallaryItem
                        image={"./data/imdb-actors/imdb-actors.png"}
                        title={"IMDb Dataset: Line Charts Embeded on Nodes"}
                        route={"/imdb-actors"}
                    >
                        Two nodes are connected if their attributes{" "}
                        <span class="description-parameter">movies</span> share
                        an intersection. Each{" "}
                        <span class="description-parameter">node</span> consists
                        of <span class="description-parameter">5</span>{" "}
                        different elements. The{" "}
                        <span class="description-parameter">first</span> element
                        is a{" "}
                        <span class="description-parameter">&lt;line&gt;</span>.
                        Its <span class="description-parameter">y1</span>{" "}
                        encodes the attribute{" "}
                        <span class="description-parameter">
                            number_of_movies_by_year@2019
                        </span>
                        . Its <span class="description-parameter">y2</span>{" "}
                        encodes the attribute{" "}
                        <span class="description-parameter">
                            number_of_movies_by_year@2020
                        </span>
                        .
                    </GallaryItem>
                    <GallaryItem
                        image={"./data/imdb-movies-link/imdb-movies-link.png"}
                        title={"IMDb Dataset: Bars on Links"}
                        route={"/imdb-movies-link"}
                    >
                        Two nodes are connected if their attributes{" "}
                        <span class="description-parameter">year</span> are
                        close, maybe with a difference less than:{" "}
                        <span class="description-parameter">5</span>. Each{" "}
                        <span class="description-parameter">node</span> consists
                        of only <span class="description-parameter">1</span>{" "}
                        element. It is a{" "}
                        <span class="description-parameter">
                            &lt;circle&gt;
                        </span>
                        . Its <span class="description-parameter">r</span>{" "}
                        encodes the attribute{" "}
                        <span class="description-parameter">avg_vote</span>.
                    </GallaryItem>
                </div>
            </div>
        </div>
    )
}

export { HomePage }
