export function Loader({ style = {}, state = "" }) {
    return (
        <div
            style={{
                color: "",
                fontFamily: "Times",
                ...style,
            }}
        >
            {state}&nbsp;&nbsp;
            <div
                className="loader-inner ball-pulse"
                style={{
                    display: "inline-block",
                }}
            >
                <div
                    style={{
                        background: "black",
                    }}
                ></div>
                <div
                    style={{
                        background: "black",
                    }}
                ></div>
                <div
                    style={{
                        background: "black",
                    }}
                ></div>
            </div>
        </div>
    )
}
