function findSimiliarColor(color) {
    const [r, g, b, _] = colorRGBA(color)
    let minDistance = Number.MAX_VALUE
    let res = 'black'
    colorTable.forEach((term) => {
        const [r_, g_, b_] = term.vec
        const d = (r - r_) ** 2 + (g - g_) ** 2 + (b - b_) ** 2
        if (d < minDistance) {
            minDistance = d
            res = term.name
        }
    })

    return res
}

export {
    findSimiliarColor
}