import { Editor } from "../components/Editor/Editor"
import { Header } from "../components/Header"
import { BsPipFill, BsPlay, BsPlayFill } from "react-icons/bs"
import { useState } from "react"
function CodeEditorPanel({ codeStr, setCodeStr, style, submit }) {
    const [modified, setModified] = useState(false)

    return (
        <div
            style={{
                ...style,
                minWidth: 300,
                // height: 1000,
                margin: 5,
                display: "flex",
                flexDirection: "column",
            }}
        >
            <Header title="Code Editor" />
            <div
                style={{
                    height: "100%",
                    marginTop: 5,
                    background: "#f6f6f6",
                    position: 'relative'
                }}
            >
                <Editor code={codeStr} setCode={(code) => {
                    setModified(true)
                    setCodeStr(code)
                }} language="js" />
                <div style={{
                    position: 'absolute',
                    right: 0,
                    top: 0,
                }}>
                    {
                        modified ? <BsPlayFill
                            fontSize={28}
                            style={{
                                zIndex: 1000,
                                color: '#3b5fc0',
                                cursor: 'pointer',
                                margin: 5,
                            }}
                            onClick={(e) => {
                                setModified(false)
                                submit()
                            }} /> : <BsPlay 
                            style={{
                                color: '#a9b0bc'
                            }}
                            fontSize={28} />
                    }
                </div>
            </div>
        </div>
    )
}

export { CodeEditorPanel }
