import { findSimiliarColor } from './color'

function processValue(value) {
    // @
    if (typeof value === 'string' && value.includes('@')) {
        value = value.replace(/@(.*)/g, (_, x) => { return `[${x}]` })
    } else if (value === 'r') {
        value = 'radius'
    }
    return value
}

export function styleParameter(parameter) {
    let type = ''
    let value = ''
    if (parameter.type) {
        type = parameter.type
        value = parameter.value
    } else {
        value = parameter
    }

    if (Array.isArray(value)) {
        return value.map(
            (_) => `<span class='description-parameter ${type}'>${processValue(_)}</span>`
            // (_) => styleParameter(_)
        )
    }

    value = processValue(value)

    // 
    if (/^#(?:[0-9a-fA-F]{3}){1,2}$/.test(value)) {
        const name = findSimiliarColor(value)
        return `<span class='description-parameter ${type}'>${name}(<span style='color: ${value};'>&#9724;</span>${value})</span>`
    }

    return `<span class='description-parameter ${type}'>${value}</span>`
}