import { Statistics } from "statistics.js"
import { BACKEND_URL } from "../configs"
import { calcDistance } from "./graphDistance"

function computePearsonCorrelation(X, Y) {
    // r
    // https://thisancog.github.io/statistics.js/inc/correlation.html
    const data = []
    for (let i = 0; i < X.length; i++) {
        data.push({ x: X[i], y: Y[i] })
    }
    const columns = {
        x: "metric",
        y: "metric",
    }
    const stats = new Statistics(data, columns)
    const r = stats.correlationCoefficient("x", "y")
    return r.correlationCoefficient
}

function computeKendallsTau(X, Y) {
    // Mann-Kendall Test for Trend
    const data = []
    for (let i = 0; i < X.length; i++) {
        data.push({ x: X[i], y: Y[i] })
    }

    data.sort((a, b) => a.x - b.x)
    const columns = {
        x: "ordinal",
        y: "ordinal",
    }
    const stats = new Statistics(data, columns)
    const r = stats.kendallsTau("x", "y")
    return r.b.tauB
}

function checkDataType(array) {
    // 
    for (let i = 0; i < array.length; i++) {
        if (isNaN(parseFloat(array[i]))) {
            return {
                first: "categorical",
                second: null,
            }
        }
    }

    // 5
    if (array.length <= 5 && array.every((x) => Number.isInteger(x))) {
        return {
            first: "categorical",
            second: "numerical",
        }
    }

    // unique30%
    if (new Set(array).size < array.length * 0.3) {
        return {
            first: "categorical",
            second: "numerical",
        }
    }

    return {
        first: "numerical",
        second: null,
    }
}

// async function attributeRelation(X, Y) {
function attributeRelation(X, Y, threshold = 0.6) {
    // console.log(X, Y)
    // 
    let dataTypeX = checkDataType(X)
    let dataTypeY = checkDataType(Y)
    // let correlationTest = null

    // 
    if (
        (dataTypeX.first === "numerical" || dataTypeX.second === "numerical") &&
        (dataTypeY.first === "numerical" || dataTypeY.second === "numerical")
    ) {
        X = X.map((x) => parseFloat(x))
        Y = Y.map((y) => parseFloat(y))
        // Pearson Correlation & Kendall's Tau
        const pearsonCorrelation = computePearsonCorrelation(X, Y)
        // const kendallsTau = computeKendallsTau(X, Y)
        // const correlationTest = await fetch(BACKEND_URL + "pearson-kendall", {
        //     method: "POST",
        //     headers: {
        //         "Content-Type": "application/json",
        //     },
        //     body: JSON.stringify({ X, Y }),
        // }).then((res) => res.json())

        if (
            // correlationTest.pearson.pvalue < 0.05 &&
            // correlationTest.kendall.pvalue < 0.05
            Math.abs(pearsonCorrelation) > threshold
        ) {
            return {
                typeX: "numerical",
                typeY: "numerical",
                correlation: pearsonCorrelation,
                // pearson: correlationTest.pearson,
                // kendall: correlationTest.kendall,
            }
        }
    }

    dataTypeX = dataTypeX.first
    dataTypeY = dataTypeY.first

    if (dataTypeX === "numerical" && dataTypeY === "numerical") {
        X = X.map((x) => parseFloat(x))
        Y = Y.map((y) => parseFloat(y))
        // Pearson Correlation & Kendall's Tau
        // const pearsonCorrelation = computePearsonCorrelation(X, Y)
        // const kendallsTau = computeKendallsTau(X, Y)
        // if (!correlationTest) {
        //     correlationTest = await fetch(BACKEND_URL + "pearson-kendall", {
        //         method: "POST",
        //         headers: {
        //             "Content-Type": "application/json",
        //         },
        //         body: JSON.stringify({ X, Y }),
        //     }).then((res) => res.json())
        // }

        // console.log(correlationTest)
        const pearsonCorrelation = computePearsonCorrelation(X, Y)

        return {
            typeX: dataTypeX,
            typeY: dataTypeY,
            correlation: pearsonCorrelation,
            // pearson: correlationTest.pearson,
            // kendall: correlationTest.kendall,
        }
    }

    if (dataTypeY === "categorical") {
        if (dataTypeX === "categorical") {
            const map = {}
            for (let i = 0; i < X.length; i++) {
                if (!map[Y[i]]) {
                    map[Y[i]] = new Set()
                }
                map[Y[i]].add(X[i])
            }
            for (const k in map) {
                map[k] = Array.from(map[k])
            }

            return {
                typeX: dataTypeX,
                typeY: dataTypeY,
                attributeMapping: map,
            }
        } else {
            const map = {}
            for (let i = 0; i < X.length; i++) {
                if (!map[Y[i]]) {
                    map[Y[i]] = { min: Number.MAX_VALUE, max: Number.MIN_VALUE }
                }
                map[Y[i]].min = Math.min(map[Y[i]].min, X[i])
                map[Y[i]].max = Math.max(map[Y[i]].max, X[i])
            }

            return {
                typeX: dataTypeX,
                typeY: dataTypeY,
                attributeMapping: map,
            }
        }
    }

    return undefined
}

function layoutRelation(graph) {
    let { euclidMatrix, floydMatrix } = calcDistance(graph)
    const N = euclidMatrix.length
    floydMatrix = floydMatrix.flat()
    euclidMatrix = euclidMatrix.flat()
    const combined = []
    for (let i = 0; i < floydMatrix.length; i++) {
        combined.push({
            floyd: floydMatrix[i],
            euclid: euclidMatrix[i],
        })
    }
    combined.sort((a, b) => (a.floyd - b.floyd))
    const data = combined.slice(N)
    // r
    // https://thisancog.github.io/statistics.js/inc/correlation.html
    const columns = {
        floyd: 'metric',
        euclid: 'metric',
    }
    const stats = new Statistics(data, columns)
    const r = stats.correlationCoefficient('floyd', 'euclid')
    return r.correlationCoefficient
}

