const THRESHOLD = 0.3 // TODO: threshold value

const getNormalizedDifference = (val1, val2, attribute) => {
    return val1 === val2
        ? 0
        : Math.abs(val1 - val2) / (attribute.max - attribute.min)
}

const intersectList = (l1, l2) => {
    return l1.filter((x) => l2.includes(x)).sort()
}

const powerSet = (array) => {
    // O(2^n)
    const results = [[]]
    for (const value of array) {
        const copy = [...results] // See note below.
        for (const prefix of copy) {
            results.push(prefix.concat(value))
        }
    }
    return results
}

function generateLinkingConditions(node1, node2, attributes) {
    const res = {}
    attributes.forEach((attr) => {
        res[attr.name] = {}
        const val1 = node1[attr.name]
        const val2 = node2[attr.name]
        if (val1 === undefined || val2 === undefined) {
            delete res[attr.name]
            return
        }
        if (attr.type === "categorical") {
            if (val1 === val2) {
                res[attr.name]["common"] = res[attr.name]["common"] || {}
                res[attr.name]["common"][val1] = 1
                res[attr.name]["common"]["@@any"] = 1
            }
        } else if (attr.type === "list") {
            // list, 
            const overlap = intersectList(val1, val2)
            if (overlap.length === 0) {
                delete res[attr.name]
                return
            }
            res[attr.name]["overlap"] = res[attr.name]["common"] || {}
            res[attr.name]["overlap"]["@@any"] = 1
            const combinations = powerSet(overlap)
            for (const arr of combinations) {
                const key = arr.join("-")
                if (key) {
                    res[attr.name]["overlap"][key] = 1
                }
            }
        } else if (attr.type === "numerical") {
            const diff = getNormalizedDifference(val1, val2, attr)
            if (diff < THRESHOLD) {
                const rawDiff = Math.abs(val1 - val2)
                // res[attr.name]['similar'] = res[attr.name]['similar'] || {}
                // res[attr.name]['similar'][rawDiff] = 1
                res[attr.name]["similar"] = rawDiff
                if (val1 === val2) {
                    res[attr.name]["common"] = res[attr.name]["common"] || {}
                    res[attr.name]["common"][val1] = 1
                    res[attr.name]["common"]["@@any"] = 1
                }
            }

            // 
            if (val1 < attr.q1 && val2 < attr.q1) {
                res[attr.name]["same-bin"] = res[attr.name]["same-bin"] || {}
                res[attr.name]["same-bin"]["low"] = 1
                res[attr.name]["same-bin"]["@@any"] = 1
            } else if (
                val1 >= attr.q1 &&
                val1 <= attr.q3 &&
                val2 >= attr.q1 &&
                val2 <= attr.q3
            ) {
                res[attr.name]["same-bin"] = res[attr.name]["same-bin"] || {}
                res[attr.name]["same-bin"]["middle"] = 1
                res[attr.name]["same-bin"]["@@any"] = 1
            } else if (val1 > attr.q3 && val2 > attr.q3) {
                res[attr.name]["same-bin"] = res[attr.name]["same-bin"] || {}
                res[attr.name]["same-bin"]["high"] = 1
                res[attr.name]["same-bin"]["@@any"] = 1
            }
        }
        if (Object.keys(res[attr.name]).length === 0) {
            delete res[attr.name]
        }
    })

    return res
}

/**
 * condition: attr-relation-value-cnt
 * @param {*} condition1
 * @param {*} condition2
 */
function unionCondition(condition1, condition2) {
    const res = Object.assign({}, condition1)
    for (const attr in condition2) {
        if (!(attr in res)) {
            res[attr] = condition2[attr]
            continue
        }
        for (const relation in condition2[attr]) {
            if (!(relation in res[attr])) {
                res[attr][relation] = condition2[attr][relation]
                continue
            }
            if (relation === "similar") {
                res[attr][relation] = Math.min(
                    res[attr][relation],
                    condition2[attr][relation]
                )
            } else {
                for (const value in condition2[attr][relation]) {
                    if (!(value in res[attr][relation])) {
                        res[attr][relation][value] =
                            condition2[attr][relation][value]
                    } else {
                        res[attr][relation][value] +=
                            condition2[attr][relation][value]
                    }
                }
            }
        }
    }
    return res
}

function accumulateCondition(condition1, condition2) {
    const res = Object.assign({}, condition1)
    for (const attr in condition2) {
        if (!(attr in res)) {
            res[attr] = condition2[attr]
            continue
        }
        for (const relation in condition2[attr]) {
            if (!(relation in res[attr])) {
                res[attr][relation] = condition2[attr][relation]
                continue
            }
            // NOTE: union
            // if (relation === 'similar') {
            // res[attr][relation] = Math.min(res[attr][relation], condition2[attr][relation])
            // } else {
            for (const value in condition2[attr][relation]) {
                if (!(value in res[attr][relation])) {
                    res[attr][relation][value] =
                        condition2[attr][relation][value]
                } else {
                    res[attr][relation][value] +=
                        condition2[attr][relation][value]
                }
            }
            // }
        }
    }
    return res
}

function excludeCondition(condition1, condition2) {
    // NOTE: may change condition1, but not condition2
    const res = Object.assign({}, condition1)
    for (const attr in condition2) {
        if (!(attr in res)) {
            continue
        }
        for (const relation in condition2[attr]) {
            if (!(relation in res[attr])) {
                continue
            }
            if (typeof res[attr][relation] === "number") {
                // NOTE: exclude
                if (res[attr][relation] >= condition2[attr][relation]) {
                    delete res[attr][relation]
                } else {
                    res[attr][relation] = { [condition2[attr][relation]]: 1 }
                }
            } else {
                for (const value in condition2[attr][relation]) {
                    if (value in res[attr][relation]) {
                        delete res[attr][relation][value]
                    }
                }
                if (Object.keys(res[attr][relation]).length === 0) {
                    delete res[attr][relation]
                }
            }
        }
        if (Object.keys(res[attr]).length === 0) {
            delete res[attr]
        }
    }
    return res
}

export {
    generateLinkingConditions,
    unionCondition,
    excludeCondition,
    accumulateCondition,
}
