import React from "react"

import { CodeEditorPanel } from "./panels/CodeEditorPanel"
import { DataEditorPanel } from "./panels/DataEditorPanel"
import { VisualizationPanel } from "./panels/VisualizationPanel"
import { VisualizationGivenDataPanel } from "./panels/VisualizationGivenDataPanel"
import { DescriptionPanel } from "./panels/DescriptionPanel"
import { getPreloadData } from "./demo/demo"
import { useEffect, useState } from "react"
import { useParams } from "react-router"
import { linkCondition } from "./linkConditions/linkCondition"
import descriptor from "./descriptor"
import * as d3 from "d3"
import { object, svgToArray } from "./descriptor/utils"
import { TitleBar } from "./components/TitleBar"
import { styleParameter } from "./utils/parameterStyle"

function Main({ title }) {
    const [runID, setRunID] = useState(0)
    const [loading, setLoading] = useState(false)
    const [dataStr, setDataStr] = useState("")
    const [codeStr, setCodeStr] = useState("")
    const [SVG, setSVG] = useState(document.createElement("svg"))
    const [visibleElements, setVisibleElements] = useState([])
    const [graphDescripitons, setGraphDescriptions] = useState({})
    const [layoutDescriptions, setLayoutDescriptions] = useState({})
    const [linkConditions, setLinkConditions] = useState([])

    // read url parameters
    const { demo } = useParams()

    useEffect(() => {
        if (demo) {
            getPreloadData(demo).then(({ dataStr, codeStr, preload }) => {
                setDataStr(dataStr)
                setCodeStr(codeStr)

                if (preload) {
                    const _ = document.createElement("div")
                    _.innerHTML = preload.svg
                    const svg = _.firstChild
                    setSVG(svg)
                    // indexelements

                    const allElements = svgToArray(svg)
                    const descriptions = preload.descriptions
                    const layoutDescriptions = preload.layoutDescriptions

                    descriptions.forEach((description) => {
                        object.dfs(description, (desc) => {
                            // desc.elements = undefined
                            if (desc.elementsIdx) {
                                desc.elements = desc.elementsIdx.map(
                                    (idx) => allElements[idx]
                                )
                            }
                        })
                    })

                    layoutDescriptions.forEach((description) => {
                        object.dfs(description, (desc) => {
                            // desc.elements = undefined
                            if (desc.elementsIdx) {
                                desc.elements = desc.elementsIdx.map(
                                    (idx) => allElements[idx]
                                )
                            }
                        })
                    })

                    // setLoading(false)
                    setVisibleElements(allElements)
                    setGraphDescriptions(descriptions)
                    setLayoutDescriptions(layoutDescriptions)
                }
            })
        }
    }, [demo])

    useEffect(() => {
        if (runID === 0) return
        // async function fetchEncodingDescriptions() {
        function fetchEncodingDescriptions() {
            try {
                const data = JSON.parse(dataStr)
                let {
                    svg,
                    allElements,
                    descriptions,
                    layoutDescriptions,
                    // } = await descriptor(codeStr, data)
                } = descriptor(codeStr, data)

                /*
                // /////////  //////////
                const svgStr = new XMLSerializer().serializeToString(svg)

                descriptions.forEach((description) => {
                    object.dfs(description, (desc) => {
                        delete desc.elements
                        desc.content = desc.template(
                            ...desc.parameters.map(styleParameter)
                        )
                    })
                })

                layoutDescriptions.forEach((description) => {
                    object.dfs(description, (desc) => {
                        delete desc.elements
                        desc.content = desc.template(
                            ...desc.parameters.map(styleParameter)
                        )
                    })
                })

                const preloadData = {
                    svg: svgStr,
                    descriptions,
                    layoutDescriptions,
                }
                console.log(JSON.stringify(preloadData))
                /////////  ////////
                */

                descriptions.forEach((description) => {
                    object.dfs(description, (desc) => {
                        desc.content = desc.template(
                            ...desc.parameters.map(styleParameter)
                        )
                    })
                })

                layoutDescriptions.forEach((description) => {
                    object.dfs(description, (desc) => {
                        desc.content = desc.template(
                            ...desc.parameters.map(styleParameter)
                        )
                    })
                })

                setSVG(svg)
                setVisibleElements(allElements)
                setGraphDescriptions(descriptions)
                setLayoutDescriptions(layoutDescriptions)
                setLoading(false)
            } catch (e) {
                console.error("invalid code or dataset.")
                console.error(e)
            }
        }
        fetchEncodingDescriptions()
    }, [runID])

    useEffect(() => {
        try {
            const conditions = linkCondition(JSON.parse(dataStr), 0.8)
            setLinkConditions(conditions)
        } catch (e) {
            console.error("Invalid dataset")
            setLinkConditions([])
        }
    }, [dataStr])

    return (
        <div style={{}}>
            <TitleBar text={title} />
            <div
                style={{
                    width: "80%",
                    maxWidth: 1000,
                    position: "absolute",
                    left: "50%",
                    transform: "translate(-50%)",
                    marginTop: 70,
                    paddingBottom: 70,
                    // height: "calc(100vh - 80px)",
                    display: "flex",
                    flexDirection: "column",
                }}
            >
                <VisualizationGivenDataPanel
                    style={{
                        margin: 5,
                    }}
                    svg={SVG}
                />
                <div
                    // id="desc-code-data"
                    style={
                        {
                            // flex: 1,
                            // height: 0,
                        }
                    }
                >
                    <DescriptionPanel
                        loading={loading}
                        linkConditions={linkConditions}
                        graphDescriptions={graphDescripitons}
                        layoutDescriptions={layoutDescriptions}
                        visibleElements={visibleElements}
                    />
                    <CodeEditorPanel
                        codeStr={codeStr}
                        setCodeStr={setCodeStr}
                        submit={() => {
                            setLoading(true)
                            setRunID(runID + 1)
                        }}
                        style={{}}
                    />
                    <DataEditorPanel
                        dataStr={dataStr}
                        setDataStr={setDataStr}
                        style={{}}
                    />
                </div>
            </div>
        </div>
    )
}

export { Main }
