function linkConditionDescription(linkConditions) {
    if (!Array.isArray(linkConditions) || linkConditions.length === 0) return ""
    // coveragecoverage
    const conditions = linkConditions
        .slice()
        .sort((a, b) => b.coverage - a.coverage)
    // condition
    const condition = conditions[0]

    if (condition.relation === "common") {
        return `Two nodes are connected if their attributes <span class='description-parameter'>${
            condition.attribute
        }</span> are with a same value${
            condition.value === "@@any"
                ? ""
                : `: <span class='description-parameter attribute-parameter'>${condition.value}</span>`
        }.`
    }
    if (condition.relation === "overlap") {
        return `Two nodes are connected if their attributes <span class='description-parameter attribute-parameter'>${
            condition.attribute
        }</span> have common values${
            condition.value === "@@any"
                ? ""
                : `: <span class='description-parameter attribute-parameter'>${condition.value}</span>`
        }.`
    }
    if (condition.relation === "similar") {
        return `Two nodes are connected if their attributes <span class='description-parameter attribute-parameter'>${condition.attribute}</span> are close, maybe with a difference less than: <span class='description-parameter'>${condition.value}</span>.`
    }
    if (condition.relation === "same-bin") {
        return `Two nodes are connected if their attributes <span class='description-parameter attribute-parameter'>${
            condition.attribute
        }</span> are within the same bin${
            condition.value === "@@any"
                ? ""
                : `: <span class='description-parameter attribute-parameter'>${condition.value}</span>`
        }.`
    }
}

export { linkConditionDescription }
