import { Header } from "../components/Header"
import { linkConditionDescription } from "../descriptions/descriptions"
import "./descriptionPanel.css"
import { Loader } from "../components/Loader"
import { Descriptions } from "../components/Descriptions"
import { Divider } from "antd"

function DescriptionPanel({
    style,
    linkConditions,
    graphDescriptions,
    layoutDescriptions,
    visibleElements,
    loading,
}) {
    const linkDescription = linkConditionDescription(linkConditions)

    return (
        <div
            style={{
                ...style,
                margin: 5,
                display: "flex",
                flexDirection: "column",
            }}
        >
            <Header title="Descriptions" />


            <div style={{
                borderLeft: "1px solid gray",
                paddingLeft: 10,
            }}>
                <Divider
                    orientation='left'
                    style={{
                        fontFamily: 'Times'
                    }}
                >Linking Condition</Divider>

                {!loading ? (
                    <div
                        style={{
                            margin: 20,
                            marginLeft: 48,
                            padding: 2,
                            fontFamily: "Times",
                        }}
                        dangerouslySetInnerHTML={{
                            __html: linkDescription,
                        }}
                    ></div>
                ) : (
                    <Loader
                        style={{
                            margin: 20,
                            marginLeft: 48,
                            padding: 2,
                            fontFamily: "Times",
                        }}
                        state={"Computing Linking Condition"}
                    ></Loader>
                )}

                <Divider
                    orientation='left'
                    style={{
                        fontFamily: 'Times'
                    }}
                >Visual Encoding</Divider>

                <Descriptions
                    loading={loading}
                    graphDescriptions={graphDescriptions}
                    visibleElements={visibleElements}
                    loadingText={'Computing Visual Encoding'}
                />
                <Divider
                    orientation='left'
                    style={{
                        fontFamily: 'Times'
                    }}
                >Layout Meaning</Divider>

                <Descriptions
                    loading={loading}
                    graphDescriptions={layoutDescriptions}
                    visibleElements={visibleElements}
                    loadingText={'Computing Layout Meaning'}
                />
            </div >
        </div>
    )
}

export { DescriptionPanel }
