import { Editor } from "../components/Editor/Editor"
import { Header } from "../components/Header"

function DataEditorPanel({ dataStr, setDataStr, style }) {
    return (
        <div
            style={{
                ...style,
                minWidth: 300,
                margin: 5,
                display: "flex",
                flexDirection: "column",
            }}
        >
            <Header title="Data Editor" />
            <div
                style={{
                    height: "100%",
                    marginTop: 5,
                    background: "#f6f6f6",
                }}
            >
                <Editor code={dataStr} setCode={setDataStr} language="js" />
            </div>
        </div>
    )
}

export { DataEditorPanel }
