import { Main } from "./Main";
import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import { HomePage } from "./pages/HomePage";
import './utils/color.js'

const titleMap = {
  'imdb-movies-shapes': 'IMDb Dataset: Nodes with Different Shapes',
  'imdb-movies': 'IMDb Dataset: Simple Nodes and Links',
  'imdb-actors': 'IMDb Dataset: Line Charts Embeded on Nodes',
  'imdb-movies-link': 'IMDb Dataset: Bars on Links'
}

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path="/">
          <HomePage />
        </Route>
        <Route path="/:demo" render={({ match }) => {
          return <Main title={titleMap[match.params.demo]} />
        }}>
        </Route>
      </Switch>
    </Router>
  )
}

export default App;
