import { Card } from "antd";
import { useHistory } from "react-router";
import './GallaryItem.css'

export function GallaryItem({ title, image, children, route }) {
    const history = useHistory()
    return (
        <Card
            hoverable
            style={{
                width: 270,
                margin: 10,
                borderRadius: 7,
            }}
            cover={<img alt={title} src={image} style={{
                borderBottom: '1px #ddd solid',
                width: 270,
                height: 270,
                objectFit: 'contain',
                borderRadius: '7px 7px 0 0',
            }} />}
            onClick={() => { history.push(route) }}
        >
            <Card.Meta style={{
                whiteSpace: 'normal',
                height: 60,
            }} title={title} />
            <div
                style={{
                    height: 75,
                    lineHeight: '25px',
                    textOverflow: 'ellipsis',
                    overflow: "hidden"
                }}
            >{children}</div>
        </Card>
    )
}