const getNumericalStats = (data, attr) => {
    const arr = data.nodes.map(n => n[attr])
    arr.sort((a, b) => a - b)
    return {
        min: arr[0],
        max: arr[arr.length - 1],
        q1: arr[parseInt(arr.length * 0.25)],
        q3: arr[parseInt(arr.length * 0.75)],
    }
}

function extractAttributes(data) {
    const res = []
    for (const [k, v] of Object.entries(data.nodes[0])) {
        if (typeof v === 'number') {
            res.push({
                name: k,
                type: 'numerical'
            })
        } else if (typeof v === 'string') {
            res.push({
                name: k,
                type: 'categorical'
            })
        } else if (Array.isArray(v)) {
            res.push({
                name: k,
                type: 'list'
            })
        }
    }

    res.forEach(attr => {
        if (attr.type === 'numerical') {
            const { min, max, q1, q3 } = getNumericalStats(data, attr.name)
            attr.min = min
            attr.max = max
            attr.q1 = q1
            attr.q3 = q3
        }
    })

    return res
}

export {
    extractAttributes
}