import { extractAttributes } from "./utils.js";
import {
    generateLinkingConditions,
    unionCondition,
    excludeCondition,
    accumulateCondition,
} from './conditionOperations.js'


function linkCondition(data, threshold = 0.8) {
    const attrs = extractAttributes(data)

    const nodeMap = {}
    data.nodes.forEach(n => {
        nodeMap[n.id] = n
    })

    // linkset

    const linkSet = new Set()

    data.links.forEach(l => {
        linkSet.add(`${l.source}+${l.target}`)
        linkSet.add(`${l.target}+${l.source}`)
    })

    // 

    let excludeUnionResult = {}

    Object.keys(nodeMap).forEach(source => {
        Object.keys(nodeMap).forEach(target => {
            const term = `${source}+${target}`
            if (source === target || linkSet.has(term)) {
                return
            }

            const conditions = generateLinkingConditions(nodeMap[source], nodeMap[target], attrs)
            excludeUnionResult = unionCondition(excludeUnionResult, conditions)
        })
    })

    /*
    console.log('invalid condition union:')
    console.log(excludeUnionResult)
    */

    // 

    let result = null

    data.links.forEach(l => {
        const sourceNode = nodeMap[l.source]
        const targetNode = nodeMap[l.target]
        const conditions = generateLinkingConditions(sourceNode, targetNode, attrs)
        if (!result) {
            result = excludeCondition(conditions, excludeUnionResult)
        } else {
            result = accumulateCondition(result, excludeCondition(conditions, excludeUnionResult))
        }
    })

    /*
    console.log('-------------------------------')
    console.log('valid condition:')
    console.log(result)
    */


    // 

    const linkCnt = data.links.length

    const possibleConditions = []

    for (const attr in result) {
        for (const relation in result[attr]) {
            if (typeof result[attr][relation] === 'number') {
                // similar 
                // TODO: similar
                continue;
            } else {
                for (const value in result[attr][relation]) {
                    if (result[attr][relation][value] > linkCnt * threshold) {
                        possibleConditions.push({
                            attribute: attr,
                            relation: relation,
                            value: value,
                            frequency: result[attr][relation][value],
                            coverage: result[attr][relation][value] / linkCnt
                        })
                    }
                }
            }
        }
    }
    /*
    console.log('-------------------------------')
    console.log('final possible conditions:')
    console.log(possibleConditions) 
    */

    return possibleConditions


    // debug

    // const link1 = data.links[1]
    // const cond1 = generateLinkingConditions(nodeMap[link1.source], nodeMap[link1.target], attrs)
    // console.log(cond1)
}

export {
    linkCondition
}