/**
 * @create date 2021-02-20 13:08:16
 * @modify date 2021-02-20 13:08:16
 * @desc entry of the repo
 */
import * as d3 from "d3"
import { mapEntity2Element, mapAttribute2Channel } from "./entity2element"
import { NLDComponents } from "./NLDComponents"
import {
    BASIC_SVG_ELEMENTS,
    COMMON_STYLE_CHANNELS,
    COMMON_POSITION_CHANNELS,
    DESCRIPTION_TEMPLATE,
    PARAMETER_TYPE,
} from "./global"
import {
    NoLinDiagram,
    number2ordinal,
    object,
    textualizeStringArray,
} from "./utils"
import {
    attributeRelation,
    layoutRelation,
} from "../attributeRelation/attributeRelation"
import { BACKEND_URL } from "../configs"

/**
 *
 * @param {string} code
 * @param {standard node-link data format} data
 */
// export default async function descriptor(code, oridata) {
export default function descriptor(code, oridata, threshold = 0.6) {
    // only keep string number undefined boolean array
    const data = object.deepcopy(oridata)
    const beginTime = performance.now()
    // eslint-disable-next-line no-new-func
    const func = new Function("d3", "data", code)
    const svg = func(d3, object.deepcopy(data))
    const nldComponents = new NLDComponents(svg)

    // Step1: Map Entities to Elements, Map Attributes to Channels
    // entity2element: [[entityIndex]: Set(elementIndex)]
    console.log(
        `%c Computing nodes' encodings...`,
        "background: #222; color: #bada55"
    )
    console.log(
        `%c Computing links' encodings...`,
        "background: #222; color: #bada55"
    )

    let [node2element, nodeElement2label] = mapEntity2Element(
        data,
        func,
        "nodes"
    )

    let [link2element, linkElement2label] = mapEntity2Element(
        data,
        func,
        "links"
    )

    // Step2: Remove Links' elements from node results
    // if link2element shares same elements with node2element,
    // we should remove them from node2element
    const element2link = []
    for (let i = 0; i < link2element.length; i++) {
        link2element[i]?.forEach((elementIndex) => {
            if (element2link[elementIndex] !== undefined) {
                // one element cannot correspond to several links
                debugger
            }
            element2link[elementIndex] = i
        })
    }

    // Remove link2element from node2element
    for (let i = 0; i < node2element.length; i++) {
        // i: nodeIndex
        const node2element_i = new Set()
        node2element[i]?.forEach((elementIndex) => {
            if (element2link[elementIndex] === undefined) {
                // the element does not correspond to a link, add it to node2element
                node2element_i.add(elementIndex)
            } else {
                delete nodeElement2label[elementIndex]
            }
        })
        node2element[i] = node2element_i
    }

    let linkElements = new Set()
    link2element.forEach((linkset) => {
        linkset.forEach((elementIndex) => {
            linkElements.add(elementIndex)
        })
    })

    node2element.forEach((nodeset) => {
        nodeset.forEach((elementIndex) => {
            if (linkElements.has(elementIndex)) {
                debugger
            }
        })
    })

    // const nodeLabel2attr2diff = await mapAttribute2Channel(
    const nodeLabel2attr2diff = mapAttribute2Channel(
        data,
        func,
        "nodes",
        node2element,
        nodeElement2label
    )

    // const linkLabel2attr2diff = await mapAttribute2Channel(
    const linkLabel2attr2diff = mapAttribute2Channel(
        data,
        func,
        "links",
        link2element,
        linkElement2label
    )

    // Step3: Generate Descriptions about Visual Encodings
    let descriptions = []
    const configs = [
        {
            entityType: "node",
            entity2element: node2element,
            element2label: nodeElement2label,
            label2attr2diff: nodeLabel2attr2diff,
        },
        {
            entityType: "link",
            entity2element: link2element,
            element2label: linkElement2label,
            label2attr2diff: linkLabel2attr2diff,
        },
    ]
    configs.forEach(
        ({ entityType, entity2element, element2label, label2attr2diff }) => {
            // Step3.1: Generate Constituents, e.g. A node can consist of 4 elements
            // const numberOfElementsEachEntity = entity2element[0].size
            let numberOfLabels = Object.keys(label2attr2diff).length
            // find related elements: all elements of nodes/links are related
            let relatedElements = []
            let relatedElementsIdx = []
            entity2element.forEach((elementSet) => {
                if (elementSet) {
                    elementSet.forEach((elementIndex) => {
                        relatedElementsIdx.push(elementIndex)
                        relatedElements.push(
                            nldComponents.basicElementArray[elementIndex]
                                .element
                        )
                    })
                }
            })

            let descrpitionSentence = {
                template: DESCRIPTION_TEMPLATE.constituent(
                    entityType,
                    numberOfLabels
                ),
                parameters: [
                    {
                        type: PARAMETER_TYPE.entity,
                        value: entityType,
                    },
                    {
                        type: PARAMETER_TYPE.value,
                        value: numberOfLabels,
                    },
                ],
                elements: relatedElements,
                elementsIdx: relatedElementsIdx,
                switch: true,
                children: [],
            }

            descriptions.push(descrpitionSentence)

            // Step3.2: Vertical Grouping
            const classes = {}
            const label2class = {}
            for (let label in label2attr2diff) {
                const attr2diff = label2attr2diff[label]
                const className = JSON.stringify(attr2diff)
                classes[className] = classes[className] ?? new Set()
                classes[className].add(label)
                label2class[label] = className
            }

            // Step3.3: Generate Encoding Descriptions for each Category
            let i = 1
            for (let label in label2attr2diff) {
                const relatedElementsIdx = element2label
                    .map((l, eleIndex) => {
                        // eslint-disable-next-line eqeqeq
                        if (l == label) {
                            return eleIndex
                        } else {
                            return undefined
                        }
                    })
                    .filter((_) => _ !== undefined)
                const relatedElements = element2label
                    .map((l, eleIndex) => {
                        // eslint-disable-next-line eqeqeq
                        if (l == label) {
                            return nldComponents.basicElementArray[eleIndex]
                                .element
                        } else {
                            return undefined
                        }
                    })
                    .filter((_) => _)

                const attr2diff = label2attr2diff[label]
                let tagNames = Array.from(
                    new Set(
                        element2label
                            .map((l, eleIndex) => {
                                // eslint-disable-next-line eqeqeq
                                if (l == label) {
                                    return nldComponents.basicElementArray[
                                        eleIndex
                                    ].tagName
                                } else {
                                    return undefined
                                }
                            })
                            .filter((_) => _)
                    )
                ).sort()

                if (tagNames.length === 1) {
                    const elementDescription = {
                        template: DESCRIPTION_TEMPLATE.onePrimitive(
                            numberOfLabels
                        ), // e.g., The first element is a rect
                        parameters: [
                            number2ordinal(i),
                            {
                                type: PARAMETER_TYPE.element,
                                value: `&lt;${tagNames[0]}&gt;`,
                            },
                        ],
                        elements: relatedElements,
                        elementsIdx: relatedElementsIdx,
                        switch: true,
                        children: [],
                    }
                    descrpitionSentence.children.push(elementDescription)

                    elementDescription.children = genDescsForAttr2Diff(
                        Object.entries(attr2diff),
                        relatedElements,
                        relatedElementsIdx
                    )
                } else if (tagNames.length > 1) {
                    const tagNameChoiceDescriptions = {
                        template: DESCRIPTION_TEMPLATE.multiPrimitive(
                            numberOfLabels
                        ),
                        parameters: [
                            number2ordinal(i),
                            {
                                type: PARAMETER_TYPE.element,
                                value: tagNames.map(
                                    (tagName) => `&lt;${tagName}&gt;`
                                ),
                            },
                        ],
                        elements: relatedElements,
                        elementsIdx: relatedElementsIdx,
                        switch: true,
                        children: [],
                    }
                    descrpitionSentence.children.push(tagNameChoiceDescriptions)

                    const tagName2attr2val = {}
                    const attr2diffEntriesWithCommonStyle = Object.entries(
                        attr2diff
                    ).reduce((result, [attrName, diff]) => {
                        if (diff.tagName) {
                            if (diff.tagName.typeX === "categorical") {
                                for (let tagName in diff.tagName
                                    .attributeMapping) {
                                    tagName2attr2val[
                                        tagName
                                    ] = tagName2attr2val[tagName] ?? {
                                        attrName,
                                    }
                                    tagName2attr2val[tagName].attrVal =
                                        tagName2attr2val[tagName].attrVal ??
                                        new Set()
                                    diff.tagName.attributeMapping[
                                        tagName
                                    ].forEach((val) => {
                                        tagName2attr2val[tagName].attrVal.add(
                                            val
                                        )
                                    })
                                }
                            } else {
                                for (let tagName in diff.tagName
                                    .attributeMapping) {
                                    tagName2attr2val[
                                        tagName
                                    ] = tagName2attr2val[tagName] ?? {
                                        attrName,
                                    }
                                    tagName2attr2val[
                                        tagName
                                    ].attrVal = tagName2attr2val[tagName]
                                        .attrVal ?? {
                                        max: -Infinity,
                                        min: Infinity,
                                    }
                                    tagName2attr2val[
                                        tagName
                                    ].attrVal.max = Math.max(
                                        tagName2attr2val[tagName].attrVal.max,
                                        Number(
                                            diff.tagName.attributeMapping.max
                                        )
                                    )
                                    tagName2attr2val[
                                        tagName
                                    ].attrVal.min = Math.min(
                                        tagName2attr2val[tagName].attrVal.min,
                                        Number(
                                            diff.tagName.attributeMapping.min
                                        )
                                    )
                                }
                            }
                        }
                        const commonStyleDiff = {}
                        if (diff.style) {
                            for (let channel in diff.style) {
                                if (
                                    COMMON_STYLE_CHANNELS.has(channel) &&
                                    diff.style[channel]
                                ) {
                                    commonStyleDiff.style =
                                        commonStyleDiff.style ?? {}
                                    commonStyleDiff.style[channel] =
                                        diff.style[channel]
                                }
                            }
                        }
                        if (Object.keys(commonStyleDiff).length) {
                            result.push([attrName, commonStyleDiff])
                        }
                        // if (!diff.tagName) {
                        //     if (diff.style) {
                        //         const _diff = {}
                        //         for (let channel in diff.style) {
                        //             if (COMMON_STYLE_CHANNELS.has(channel)) {
                        //                 _diff.style = _diff.style ?? {}
                        //                 _diff.style[channel] = true
                        //             }
                        //         }
                        //         if (_diff.style) {
                        //             result.push([attrName, _diff])
                        //         }
                        //     }
                        // } else {
                        //     result.push([attrName, { tagName: true }])
                        // }
                        return result
                    }, [])

                    // attr2diffEntriesWithCommonStyle.forEach(
                    //     ([attrName, diff]) => {
                    //         const channels = []
                    //         if (diff.tagName) {
                    //             channels.push("tagName")
                    //         }
                    //         if (diff.style) {
                    //             Object.entries(diff.style).forEach(
                    //                 ([channel, bool]) => {
                    //                     if (bool) {
                    //                         channels.push(
                    //                             `"${channel.split(".")[0]}"`
                    //                         )
                    //                     }
                    //                 }
                    //             )
                    //         }
                    //         descrpitionSentence.children.push({
                    //             template: DESCRIPTION_TEMPLATE.channels2attribute(),
                    //             parameters: [channels, attrName],
                    //             elements: relatedElements,
                    //             switch: false,
                    //             children: [],
                    //         })
                    //     }
                    // )
                    genDescsForAttr2Diff(
                        attr2diffEntriesWithCommonStyle,
                        relatedElements,
                        relatedElementsIdx
                    ).forEach((desc) => {
                        descrpitionSentence.children.push(desc)
                    })

                    tagNames.forEach((tagName) => {
                        const thisTagNameRelatedElements = relatedElements.filter(
                            (element) => element.tagName === tagName
                        )
                        const thisTagNameRelatedElementsIdx = relatedElementsIdx.filter(
                            (idx, i) => relatedElements[i].tagName === tagName
                        )
                        // TODO: tagName2attr2val
                        // multiPrimitiveTagName: (attrName, attrVals, tagName) => {}
                        const valueArrayOfAttributesControlsThisTag =
                            "max" in tagName2attr2val[tagName].attrVal &&
                            "min" in tagName2attr2val[tagName].attrVal
                                ? [
                                      tagName2attr2val[tagName].attrVal.min,
                                      tagName2attr2val[tagName].attrVal.max,
                                  ]
                                : Array.from(tagName2attr2val[tagName].attrVal)
                        const thisTagNameDescriptions = {
                            template: DESCRIPTION_TEMPLATE.multiPrimitiveTagName(
                                tagName2attr2val[tagName].attrVal
                            ),
                            parameters: [
                                {
                                    // tagName
                                    type: PARAMETER_TYPE.element,
                                    value: `&lt;${tagName}&gt;`,
                                },
                                {
                                    // attrName
                                    type: PARAMETER_TYPE.attribute,
                                    value: tagName2attr2val[tagName].attrName,
                                },
                                ...valueArrayOfAttributesControlsThisTag.map(
                                    (value) => ({
                                        // attrVal
                                        type: PARAMETER_TYPE.value,
                                        value,
                                    })
                                ),
                            ],
                            elements: thisTagNameRelatedElements,
                            elementsIdx: thisTagNameRelatedElementsIdx,
                            switch: true,
                            children: [],
                        }

                        tagNameChoiceDescriptions.children.push(
                            thisTagNameDescriptions
                        )

                        const attr2diffEntriesWithThisTag = Object.entries(
                            attr2diff
                        ).reduce((result, [attrName, diff]) => {
                            const thisTagDiff = {}
                            if (diff.style) {
                                for (let channel in diff.style) {
                                    if (channel.split(".")[1] === tagName) {
                                        channel = channel.split(".")[0]
                                    }
                                    if (
                                        BASIC_SVG_ELEMENTS.get(tagName).has(
                                            channel
                                        ) &&
                                        !COMMON_STYLE_CHANNELS.has(channel)
                                    ) {
                                        thisTagDiff.style =
                                            thisTagDiff.style ?? {}
                                        thisTagDiff.style[channel] =
                                            diff.style[channel]
                                    }
                                }
                            }
                            if (Object.keys(thisTagDiff).length) {
                                result.push([attrName, thisTagDiff])
                            }
                            // if (!diff.tagName) {
                            //     if (diff.style) {
                            //         const _diff = {}
                            //         for (let channel in diff.style) {
                            //             if (channel.split(".")[1] === tagName) {
                            //                 channel = channel.split(".")[0]
                            //             }
                            //             if (
                            //                 BASIC_SVG_ELEMENTS.get(tagName).has(
                            //                     channel
                            //                 ) &&
                            //                 !COMMON_STYLE_CHANNELS.has(channel)
                            //             ) {
                            //                 _diff.style = _diff.style ?? {}
                            //                 _diff.style[channel] = true
                            //             }
                            //             // if (
                            //             //     [
                            //             //         COMMON_POSITION_CHANNELS.get(
                            //             //             tagName
                            //             //         ),
                            //             //         BASIC_SVG_ELEMENTS.get(tagName),
                            //             //     ].some(
                            //             //         (channels) =>
                            //             //             channels.has(channel) ||
                            //             //             (channel.split(".")[1] ==
                            //             //                 tagName &&
                            //             //                 channels.has(
                            //             //                     channel.split(
                            //             //                         "."
                            //             //                     )[0]
                            //             //                 ))
                            //             //     )
                            //             // ) {
                            //             //     _diff.style = _diff.style ?? {}
                            //             //     _diff.style[channel] = true
                            //             // }
                            //         }
                            //         if (_diff.style) {
                            //             result.push([attrName, _diff])
                            //         }
                            //     }
                            // }
                            return result
                        }, [])

                        thisTagNameDescriptions.children = genDescsForAttr2Diff(
                            attr2diffEntriesWithThisTag,
                            thisTagNameRelatedElements,
                            thisTagNameRelatedElementsIdx
                        )
                        // attr2diffEntriesWithThisTag.forEach(
                        //     ([attrName, diff], j) => {
                        //         const channels = []
                        //         if (diff.style) {
                        //             Object.entries(diff.style).forEach(
                        //                 ([channel, bool]) => {
                        //                     if (bool) {
                        //                         channels.push(
                        //                             `"${channel.split(".")[0]}"`
                        //                         )
                        //                     }
                        //                 }
                        //             )
                        //         }
                        //         thisTagNameDescriptions.children.push({
                        //             template: DESCRIPTION_TEMPLATE.channels2attribute(
                        //                 /*isfirstWordUpper*/ j !== 0
                        //             ),
                        //             parameters: [channels, attrName],
                        //             elements: thisTagNameRelatedElements,
                        //             switch: false,
                        //             children: [],
                        //         })
                        //     }
                        // )
                    })
                }

                i++
            }
        }
    )

    function genDescsForAttr2Diff(
        attr2diffEntries,
        relatedElements,
        relatedElementsIdx
    ) {
        const result = []
        attr2diffEntries.forEach(([attrName, diff]) => {
            const channels = []
            if (diff.tagName) {
                channels.push("tagName")
            }
            if (diff.style) {
                Object.entries(diff.style).forEach(([channel, bool]) => {
                    if (bool) {
                        channels.push(`${channel.split(".")[0]}`)
                    }
                })
            }

            // Generate correlations
            channels.forEach((channel) => {
                let relation
                if (channel === "tagName") {
                    relation = diff.tagName
                } else {
                    relation = diff.style[channel]
                }

                let encodingDetailDescription = {
                    template: DESCRIPTION_TEMPLATE.channels2attribute(), // Its fill encodes the attribute xxx.
                    parameters: [
                        {
                            type: PARAMETER_TYPE.channel,
                            value: [channel],
                        },
                        {
                            type: PARAMETER_TYPE.attribute,
                            value: attrName,
                        },
                    ],
                    elements: relatedElements,
                    elementsIdx: relatedElementsIdx,
                    switch: false,
                    children: [],
                }
                if (relation && relation !== true) {
                    // For numerical to numerical: describe the kendall test
                    if (
                        relation.typeX === "numerical" &&
                        relation.typeY === "numerical" &&
                        // relation.kendall.pvalue <= 0.05
                        Math.abs(relation.correlation) > threshold
                    ) {
                        // more...more...
                        encodingDetailDescription.children.push({
                            template: DESCRIPTION_TEMPLATE.correlationN2N(
                                // relation.pearson.correlation > 0
                                relation.correlation > 0
                            ), // Its fill encodes the attribute xxx.
                            parameters: [
                                {
                                    type: PARAMETER_TYPE.channel,
                                    value: channel,
                                },
                                {
                                    type: PARAMETER_TYPE.attribute,
                                    value: attrName,
                                },
                            ],
                            elements: relatedElements,
                            elementsIdx: relatedElementsIdx,
                        })
                    } else if (
                        relation.typeX === "numerical" &&
                        relation.typeY === "categorical"
                    ) {
                        Object.entries(relation.attributeMapping).forEach(
                            ([chanVal, attrVals]) => {
                                encodingDetailDescription.children.push({
                                    template:
                                        DESCRIPTION_TEMPLATE.correlationN2C, // `When the value of the attribute ${attrName} is from ... to ..., its ${chanName} turns to ${chanVal}.`
                                    parameters: [
                                        {
                                            type: PARAMETER_TYPE.channel,
                                            value: channel,
                                        },
                                        {
                                            type: PARAMETER_TYPE.attribute,
                                            value: attrName,
                                        },
                                        {
                                            type: PARAMETER_TYPE.value,
                                            value: chanVal,
                                        },
                                        {
                                            type: PARAMETER_TYPE.value,
                                            value: attrVals.min,
                                        },
                                        {
                                            type: PARAMETER_TYPE.value,
                                            value: attrVals.max,
                                        },
                                    ],
                                    elements: relatedElements.filter(
                                        (element) => {
                                            if (channel !== "tagName") {
                                                return (
                                                    element.attributes[channel]
                                                        .value == chanVal
                                                )
                                            } else {
                                                return (
                                                    element.tagName == chanVal
                                                )
                                            }
                                        }
                                    ),
                                    elementsIdx: relatedElementsIdx.filter(
                                        (idx, i) => {
                                            if (channel !== "tagName") {
                                                return (
                                                    relatedElements[i]
                                                        .attributes[channel]
                                                        .value == chanVal
                                                )
                                            } else {
                                                return (
                                                    relatedElements[i]
                                                        .tagName == chanVal
                                                )
                                            }
                                        }
                                    ),
                                })
                            }
                        )
                    } else if (
                        relation.typeX === "categorical" &&
                        relation.typeY === "categorical" &&
                        relation.attributeMapping
                    ) {
                        Object.entries(relation.attributeMapping).forEach(
                            ([chanVal, attrVals]) => {
                                encodingDetailDescription.children.push({
                                    template:
                                        DESCRIPTION_TEMPLATE.correlationC2C, // `When the value of the attribute ${attrName} is ${textualizeStringArray( attrVals, "or" )}, its ${chanName} turns to ${chanVal}.`
                                    parameters: [
                                        {
                                            type: PARAMETER_TYPE.channel,
                                            value: channel,
                                        },
                                        {
                                            type: PARAMETER_TYPE.attribute,
                                            value: attrName,
                                        },
                                        {
                                            type: PARAMETER_TYPE.value,
                                            value: chanVal,
                                        },
                                        {
                                            type: PARAMETER_TYPE.value,
                                            value: attrVals,
                                        },
                                    ],
                                    elements: relatedElements.filter(
                                        (element) => {
                                            if (channel !== "tagName") {
                                                return (
                                                    element.attributes[channel]
                                                        .value == chanVal
                                                )
                                            } else {
                                                return (
                                                    element.tagName == chanVal
                                                )
                                            }
                                        }
                                    ),
                                    elementsIdx: relatedElementsIdx.filter(
                                        (idx, i) => {
                                            if (channel !== "tagName") {
                                                return (
                                                    relatedElements[i]
                                                        .attributes[channel]
                                                        .value == chanVal
                                                )
                                            } else {
                                                return (
                                                    relatedElements[i]
                                                        .tagName == chanVal
                                                )
                                            }
                                        }
                                    ),
                                })
                            }
                        )
                    }
                }

                if (encodingDetailDescription.children.length == 0) {
                    encodingDetailDescription.children.push({
                        template: DESCRIPTION_TEMPLATE.correlationUndefined, // `However, the correlation between ${channel} and ${attrName} are not determined yet.`
                        parameters: [
                            {
                                type: PARAMETER_TYPE.channel,
                                value: channel,
                            },
                            {
                                type: PARAMETER_TYPE.attribute,
                                value: attrName,
                            },
                        ],
                        elements: relatedElements,
                        elementsIdx: relatedElementsIdx,
                    })
                }

                result.push(encodingDetailDescription)
            })
            // For numerical to categorical:
            // For categorical to categorical:
        })
        return result
    }

    // Step4: Detect Layout Meanings
    // Step4.1: Generate x/y positions for each node
    const nodeAttrs = Array.from(
        NoLinDiagram.getAttributesOf(data).nodes.entries()
    ) //! should before position computing

    document.body.appendChild(svg)
    const svgBBox = svg.getBoundingClientRect()
    node2element.forEach((elementIndexSet, entityIndex) => {
        const elements = Array.from(elementIndexSet).map(
            (elementIndex) =>
                nldComponents.basicElementArray[elementIndex].element
        )

        const min = {
            x: Infinity,
            y: Infinity,
        }
        const max = {
            x: -Infinity,
            y: -Infinity,
        }

        elements.forEach((element) => {
            const box = element.getBoundingClientRect()
            const x = box.x - svgBBox.x
            const y = box.y - svgBBox.y
            min.x = Math.min(min.x, x + box.width / 2)
            max.x = Math.max(max.x, x + box.width / 2)

            min.y = Math.min(min.y, y + box.height / 2)
            max.y = Math.max(max.y, y + box.height / 2)
        })

        data.nodes[entityIndex].x = (max.x + min.x) / 2
        data.nodes[entityIndex].y = (max.y + min.y) / 2
    })
    document.body.removeChild(svg)
    const endTime = performance.now()
    console.log("Computing time:", endTime - beginTime, "ms")

    const allElements = nldComponents.basicElementArray.map((_) => _.element)
    const allElementsIdx = new Array(nldComponents.basicElementArray.length)
        .fill()
        .map((_, i) => i)
    const xs = data.nodes.map((n) => n.x)
    const ys = data.nodes.map((n) => n.y)
    const xAttrs = []
    const yAttrs = []

    for (let i = 0; i < nodeAttrs.length; i++) {
        const attrName = nodeAttrs[i][0]
        // const CONNECTOR = "@"
        let attrVals = nodeAttrs[i][1].range
        // if (attrName.indexOf(CONNECTOR) > 0) {
        //     attrVals = data.nodes.map(
        //         (n) =>
        //             n[attrName.split(CONNECTOR)[0]][
        //                 attrName.split(CONNECTOR)[1]
        //             ]
        //     )
        // } else {
        //     attrVals = data.nodes.map((n) => n[attrName])
        // }

        // console.log(i, nodeAttrs.length, attrName, attrVals)
        // const xRelation = await attributeRelation(xs, attrVals)
        const xRelation = attributeRelation(xs, attrVals)
        // if (xRelation?.pearson?.pvalue <= 0.05) {
        if (Math.abs(xRelation?.correlation) > threshold) {
            // console.log(attrName, "x")
            xAttrs.push({
                name: attrName,
                // type: xRelation?.pearson?.correlation > 0,
                type: xRelation?.correlation > 0,
            })
        }
        // const yRelation = await attributeRelation(ys, attrVals)
        const yRelation = attributeRelation(ys, attrVals)
        // if (yRelation?.pearson?.pvalue <= 0.05) {
        if (Math.abs(yRelation?.correlation) > threshold) {
            // console.log(attrName, "y")
            yAttrs.push({
                name: attrName,
                // type: yRelation?.pearson?.correlation > 0,
                type: yRelation?.correlation > 0,
            })
        }
    }

    // console.log(xAttrs, yAttrs)
    const layoutDescriptions = []
    if (xAttrs.length > 0 && yAttrs.length > 0) {
        // attribute-layout
        ;[
            { axis: "x", attrs: xAttrs },
            { axis: "y", attrs: yAttrs },
        ].forEach(({ axis, attrs }) => {
            layoutDescriptions.push({
                template: (axis, attrNames) => {
                    return `The ${axis}-coordinates encodes ${
                        attrNames.length > 1 ? "" : "the"
                    } attribute${
                        attrNames.length > 1 ? "" : "s"
                    } ${textualizeStringArray(attrNames)}`
                },
                parameters: [
                    {
                        type: PARAMETER_TYPE.channel,
                        value: axis,
                    },
                    {
                        type: PARAMETER_TYPE.attribute,
                        value: attrs.map((_) => _.name),
                    },
                ],
                elements: [],
                elementsIdx: [],
                children: attrs.map((attr) => ({
                    template: (axis, attrName) => {
                        return `The greater the ${attrName} is, the ${
                            attr.type ? "greater" : "smaller"
                        } the ${axis}-coordinate is.`
                    },
                    parameters: [
                        {
                            type: PARAMETER_TYPE.channel,
                            value: axis,
                        },
                        {
                            type: PARAMETER_TYPE.attribute,
                            value: attr.name,
                        },
                    ],
                    elements: allElements,
                    elementsIdx: allElementsIdx,
                })),
            })
        })
    }

    // // topology-layout
    // const correlation = await fetch(BACKEND_URL + "layout-correlation", {
    //     method: "POST",
    //     headers: {
    //         "Content-Type": "application/json",
    //     },
    //     body: JSON.stringify(data),
    // }).then((res) => res.json())
    const correlation = layoutRelation(data)

    if (Math.abs(correlation) > 0.3) {
        layoutDescriptions.push({
            template: () => {
                return `The layout is a topology-based layout, which means the farther the topology distance between two nodes, the farther the distance between them.`
            },
            parameters: [],
            elements: allElements,
            elementsIdx: allElementsIdx,
        })
    } else if (xAttrs.length === 0 && yAttrs.length === 0) {
        layoutDescriptions.push({
            template: () => {
                return `The layout is neither an attribute-based layout nor a topology-based layout.`
            },
            parameters: [],
            elements: allElements,
            elementsIdx: allElementsIdx,
        })
    }

    // console.log(layoutDescriptions)

    return {
        svg,
        allElements,
        allElementsIdx,
        descriptions,
        layoutDescriptions,
    }
}
