export function Header({ title }) {
    return (
        <div
            style={{
                height: 50,
                // width: 'calc(100% - 10px)',
                width: "auto",
                lineHeight: "50px",
                fontWeight: "bold",
                borderLeft: "1px solid gray",
                fontSize: "2em",
                fontFamily: "Times",
                color: "#333333",
                textIndent: 20,
            }}
        >
            {title}
        </div>
    )
}
