import { useEffect, useRef } from "react"
import * as d3 from "d3"
import { Header } from "../components/Header"

function VisualizationGivenDataPanel({ style, svg }) {
    const ref = useRef()

    useEffect(() => {
        ref.current.innerHTML = ""
        if (svg) {
            ref.current.appendChild(svg)
        }
    }, [svg])

    return (
        <div style={style}>
            <Header title="Node-Link Diagram" />
            <div
                style={{
                    marginTop: 10,
                    borderLeft: "1px solid gray",
                }}
                ref={ref}
            >
            </div>
        </div>
    )
}

export { VisualizationGivenDataPanel }
