// great: it is a good channel to encode info
// maybe: it is not really good enough

import { number2ordinal, textualizeStringArray, textualizeVaringStringArray } from "./utils"

const SUPPORTED_TYPES = new Set(["string", "number", "undefined", "boolean"])

// rare: it is bad to encode info
const COMMON_STYLE_CHANNELS = new Set([
    "fill", // great, color
    "fill-opacity", // maybe, numerical
    "stroke", // great, color
    "stroke-dasharray", // rare
    "stroke-opacity", // maybe, numerical
    "stroke-width", // great, numerical
])

const COMMON_POSITION_CHANNELS = new Map([
    ["circle", new Set(["cx", "cy"])], // great, numerical
    ["ellipse", new Set(["cx", "cy"])], // great, numerical
    ["line", new Set(["x1", "x2", "y1", "y2"])], // great, numerical
    ["polygon", new Set(["points"])], // great, numerical array
    ["polyline", new Set(["points"])], // great, numerical array
    ["rect", new Set(["x", "y"])], // great, rx/ry: mayby, all numerical
    ["path", new Set([])], // great, categorical + numerical
])

const DEFAULT_ATTRIBUTE = {
    fill: "#000000",
    "fill-opacity": 1,
    stroke: "#000000",
    "stroke-dasharray": "none",
    "stroke-opacity": 1,
    rx: "auto",
    ry: "auto",
}

const BASIC_SVG_ELEMENTS = new Map([
    ["circle", ["r"]], // great, numerical
    ["ellipse", ["rx", "ry"]], // great, numerical
    ["line", []], // great, numerical
    ["polygon", []], // great, numerical array
    ["polyline", []], // great, numerical array
    ["rect", ["width", "height", "rx", "ry"]], // great, rx/ry: mayby, all numerical
    ["path", []], // great, categorical + numerical
])
BASIC_SVG_ELEMENTS.forEach((attributes, name) => {
    attributes = attributes.concat([...COMMON_STYLE_CHANNELS])
    BASIC_SVG_ELEMENTS.set(name, new Set(attributes))
})

const CONNECTOR_CHAR = ""

const SHARED_CHANNELS = new Set(["rx", "ry", "cx", "cy", "points"])

const DESCRIPTION_TEMPLATE = {
    constituent: (entityType, numberOfElementsEachEntity) => {
        return (_entityType, _numberOfElementsEachEntity) => {
            return `Each ${_entityType} consists of ${
                numberOfElementsEachEntity > 1 ? "" : "only"
            } ${_numberOfElementsEachEntity} ${
                numberOfElementsEachEntity > 1 ? "different" : ""
            } element${numberOfElementsEachEntity > 1 ? "s" : ""}.`
        }
    },
    onePrimitive: (numberOfLabels) => {
        return (ordinal, tagName) => {
            if (numberOfLabels > 1) {
                return `The ${ordinal} element is a ${tagName}.`
            } else {
                return `It is a ${tagName}.`
            }
        }
    },
    multiPrimitive: (numberOfLabels) => {
        return (ordinal, tagNames) => {
            if (numberOfLabels > 1) {
                return `For the ${ordinal} element, its <span class='description-parameter channel-parameter'>tagName</span> varies among multiple shapes: ${textualizeVaringStringArray(
                    tagNames
                )}.`
            } else {
                return `Its <span class='description-parameter channel-parameter'>tagName</span> varies among multiple shapes: ${textualizeVaringStringArray(
                    tagNames
                )}.`
            }
        }
    },
    multiPrimitiveTagName: (attrVal) => {
        return (_tagName, _attrName, ..._attrVal) => {
            // when the value of attrName is from attrVal.min to attrVal.max,
            // or the value of attrName is attrVal[0], ..., or attrVal[-1],
            _attrVal.sort()
            if ("max" in attrVal && "min" in attrVal) {
                return `When the value of ${_attrName} is from ${_attrVal[0]} to ${_attrVal[1]}, its <span class='description-parameter channel-parameter'>tagName</span> is changed into ${_tagName}.`
            } else {
                return `When the value of ${_attrName} is ${textualizeStringArray(
                    _attrVal,
                    "or"
                )}, its <span class='description-parameter channel-parameter'>tagName</span> is changed into ${_tagName}.`
            }
        }
    },
    channels2attribute: (isfirstWordUpper = true) => {
        return (channels, attribute) => {
            return `${isfirstWordUpper ? "I" : "i"}ts ${textualizeStringArray(
                channels
            )} encode${
                channels.length > 1 ? "" : "s"
            } the attribute ${attribute}.`
        }
    },
    samePrimitive: (sameLabelIs) => {
        return (sameLabelIBegin, sameLabelIsStop, ordinal) => {
            if (sameLabelIs.length > 1) {
                return `For the ${sameLabelIBegin} to ${sameLabelIsStop} elements, its encoding scheme is same to the ${ordinal} element.`
            } else {
                return `For the ${sameLabelIBegin} element, its encoding scheme is same to the ${ordinal} element.`
            }
        }
    },
    correlationC2C: (chanName, attrName, chanVal, attrVals) => {
        return `When the value of ${attrName} is ${textualizeStringArray(
            attrVals.sort(),
            "or"
        )}, its ${chanName} is changed into ${chanVal}.`
    },

    correlationN2C: (chanName, attrName, chanVal, attrValMin, attrValMax) => {
        return `When the value of ${attrName} is from ${attrValMin} to ${attrValMax}, its ${chanName} is changed into ${chanVal}.`
    },
    correlationN2N: (isUp) => {
        return (chanName, attrName) => {
            return `The greater ${attrName}, the ${
                isUp ? "greater" : "smaller"
            } its ${chanName}.`
        }
    },
    correlationUndefined: (chanName, attrName) => {
        return `The correlation between ${chanName} and ${attrName} can not be determined yet.`
    },
}

const PARAMETER_TYPE = {
    entity: "entity-parameter",
    element: "element-parameter",
    channel: "channel-parameter",
    attribute: "attribute-parameter",
    value: "value-parameter",
}

export {
    PARAMETER_TYPE,
    BASIC_SVG_ELEMENTS,
    COMMON_STYLE_CHANNELS,
    DEFAULT_ATTRIBUTE,
    CONNECTOR_CHAR,
    COMMON_POSITION_CHANNELS,
    SHARED_CHANNELS,
    DESCRIPTION_TEMPLATE,
    SUPPORTED_TYPES,
}
