const demos = {
    miserables: {
        data: "miserables/miserables_5.json",
        code: "miserables/code.js",
    },
    nodeEncoding: {
        data: "nodeEncoding/data.json",
        code: "nodeEncoding/code.js",
    },
    "imdb-movies": {
        data: "imdb-movies-raw/data.json",
        code: "imdb-movies-raw/code.js",
        preload: "imdb-movies-raw/preload.json",
        // data: "imdb-movies/data.json",
        // code: "imdb-movies/code.js",
        // preload: "imdb-movies/preload.json"
    },
    "imdb-actors": {
        data: "imdb-actors-raw/data.json",
        code: "imdb-actors-raw/code.js",
        preload: "imdb-actors-raw/preload.json",
        // data: "imdb-actors/data.json",
        // code: "imdb-actors/code.js",
        // preload: "imdb-actors/preload.json"
    },
    "imdb-movies-link": {
        data: "imdb-movies-link/data.json",
        code: "imdb-movies-link/code.js",
        preload: "imdb-movies-link/preload.json",
        // data: "imdb-movies/data.json",
        // code: "imdb-movies/code.js",
        // preload: "imdb-movies/preload.json"
    },
    "imdb-movies-shapes": {
        data: "imdb-movies-shapes/data.json",
        code: "imdb-movies-shapes/code.js",
        preload: "imdb-movies-shapes/preload.json",
        // data: "imdb-movies/data.json",
        // code: "imdb-movies/code.js",
        // preload: "imdb-movies/preload.json"
    },
}

async function getPreloadData(demo) {
    try {
        // 
        const dataStr = await fetch(`data/${demos[demo].data}`).then((res) =>
            res.text()
        )
        const codeStr = await fetch(`data/${demos[demo].code}`).then((res) =>
            res.text()
        )
        const preload = await fetch(`data/${demos[demo].preload}`).then((res) =>
            res.json()
        )

        return {
            dataStr,
            codeStr,
            preload,
        }
    } catch (e) {
        console.error(e)
        return {
            dataStr: "",
            codeStr: "",
            preload: null,
        }
    }
}

export { getPreloadData }
