function calcEulerDistance(graph) {
    const N = graph.nodes.length
    const matrix = Array(N).fill().map(() => Array(N).fill(0))
    const idToIdx = {}
    graph.nodes.forEach((n, i) => {
        idToIdx[n.id] = i
    })

    graph.nodes.forEach((a, i) => {
        graph.nodes.forEach((b, j) => {
            if (i !== j)
                matrix[i][j] = Math.sqrt((a.x - b.x) ** 2 + (a.y - b.y) ** 2)
        })
    })

    return matrix
}

function floyd(graph) {
    let INF = Infinity;
    const id_to_index = new Map();
    let n = graph["nodes"].length;
    let matrix = [];

    for (let i = 0; i < n; ++i) {
        id_to_index.set(graph["nodes"][i]["id"], i);

        const unit = [];
        for (let j = 0; j < n; ++j) {
            unit.push(INF);
        }
        matrix.push(unit);
    }

    for (let i = 0; i < n; ++i) {
        matrix[i][i] = 0;
    }

    for (let i = 0; i < graph["links"].length; ++i) {
        let fr = id_to_index.get(graph["links"][i]["source"]);
        let to = id_to_index.get(graph["links"][i]["target"]);
        matrix[fr][to] = 1
        matrix[to][fr] = 1
    }

    for (let k = 0; k < n; ++k) {
        for (let i = 0; i < n; ++i) {
            for (let j = 0; j < n; ++j) {
                if (matrix[i][k] + matrix[k][j] < matrix[i][j]) {
                    matrix[i][j] = matrix[i][k] + matrix[k][j];
                }
            }
        }
    }

    return matrix;
}

function calcDistance(graph) {
    const N = graph.nodes.length
    const idToIdx = {}
    graph.nodes.forEach((n, i) => {
        idToIdx[n.id] = i
    })

    const euclidMatrix = Array(N).fill().map(() => Array(N).fill(0))
    graph.nodes.forEach((a, i) => {
        graph.nodes.forEach((b, j) => {
            if (i !== j)
                euclidMatrix[i][j] = Math.sqrt((a.x - b.x) ** 2 + (a.y - b.y) ** 2)
        })
    })

    const floydMatrix = Array(N).fill().map(() => Array(N).fill(Infinity))
    for (let i = 0; i < N; ++i) {
        floydMatrix[i][i] = 0;
    }

    for (let i = 0; i < graph["links"].length; ++i) {
        let fr = idToIdx[graph["links"][i]["source"]];
        let to = idToIdx[graph["links"][i]["target"]];
        floydMatrix[fr][to] = 1
        floydMatrix[to][fr] = 1
    }

    for (let k = 0; k < N; ++k) {
        for (let i = 0; i < N; ++i) {
            for (let j = 0; j < N; ++j) {
                if (floydMatrix[i][k] + floydMatrix[k][j] < floydMatrix[i][j]) {
                    floydMatrix[i][j] = floydMatrix[i][k] + floydMatrix[k][j];
                }
            }
        }
    }

    return {
        euclidMatrix,
        floydMatrix
    }
}

export {
    calcDistance
}